<template>
  <div class="orders">
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="8">
          <!--  -->
          <el-input v-model="queryInfo.query" class="input-with-select" clearable placeholder=""
                    @keyup.enter.native="getOrdersList">
            <el-button slot="append" icon="el-icon-search" @click="getOrdersList"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!---->
      <el-table v-loading="!orderList.length"
                :data="orderList"
                :stripe="true"
                border
                element-loading-text=""
                width="100%">
        <el-table-column label="#"
                         type="index">
        </el-table-column>
        <el-table-column label=""
                         prop="order_number">
        </el-table-column>
        <el-table-column label=""
                         prop="order_price">
        </el-table-column>
        <el-table-column label=""
                         prop="pay_status">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.pay_status === '1'" type="success"></el-tag>
            <el-tag v-else type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label=""
                         prop="is_send">
        </el-table-column>
        <el-table-column label=""
                         prop="create_time">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tooltip :enterable="false" class="item" content="" effect="dark" placement="top">
              <el-button icon="el-icon-edit" size="mini" type="primary" @click="showBox"></el-button>
            </el-tooltip>
            <el-tooltip :enterable="false" class="item" content="" effect="dark" placement="top">
              <el-button icon="el-icon-location" size="mini" type="success"
                         @click="showProgressBox(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-pagination
        :current-page="queryInfo.pagenum"
        :page-size="queryInfo.pagesize"
        :page-sizes="[5,10,15,20]"
        :total="total"
        background
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange">
      </el-pagination>
    </el-card>

    <!---->
    <el-dialog
      :visible.sync="addressVisible"
      title=""
      width="35%"
      @close="addressDialogClosed">
      <el-form ref="addressFormRef" :model="addressForm" :rules="addressFormRules"
               label-width="100px">
        <el-form-item label="/" prop="address1">
          <el-cascader
            v-model="addressForm.address1"
            :options="cityData"
            :props="cityProps"
            clearable
            expand-trigger="hover"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="detailAdd">
          <el-input v-model="addressForm.detailAdd"></el-input>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--  -->
    <el-dialog
      :visible.sync="progressVisible"
      title=""
      width="35%">
      <!--
        reverse="true" 
      -->
      <el-timeline :reverse="true">
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time">
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
      <span slot="footer" class="dialog-footer">
          <el-button @click="progressVisible = false"> </el-button>
          <el-button type="primary" @click="progressVisible = false"> </el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>

import cityData from '../../utils/citydata'

export default {
  /* eslint-disable */
  name: 'Order',
  data () {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      total: 0,
      orderList: [],
      addressVisible: false,
      addressForm: {
        address1: [],
        detailAdd: ''
      },
      addressFormRules: {
        address1: [
          {
            required: true,
            message: '/',
            trigger: 'blur'
          }
        ],
        detailAdd: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      // /
      cityData: cityData,
      cityProps: {
        // el-cascader,cascadeProps
        expandTrigger: 'hover'
      },
      progressVisible: false,
      progressInfo: []
    }
  },
  created () {
    this.getOrdersList()
  },
  methods: {
    /**
     * 
     */
    async getOrdersList () {
      const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$http.error('!')
      } else {
        this.total = res.data.total
        this.orderList = res.data.goods
      }
    },
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrdersList()
    },
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrdersList()
    },
    /**
     * 
     */
    showBox () {
      this.addressVisible = true
    },
    /**
     *  
     */
    addressDialogClosed () {
      this.$refs.addressFormRef.resetFields()
    },
    /**
     * 
     * @returns {Promise<*>}
     */
    async showProgressBox (row) {
      // 
      // const { data: res } = await this.$http.get('/kuaidi/804909574412544580')
      // if (res.meta.status !== 200) {
      //   return this.$http.error('')
      // }

      this.progressInfo = [
        {
          time: '2018-05-10 09:39:00',
          ftime: '2018-05-10 09:39:00',
          context: ',,',
          location: ''
        },
        {
          time: '2018-05-10 08:23:00',
          ftime: '2018-05-10 08:23:00',
          context: '[]  95338',
          location: ''
        },
        {
          time: '2018-05-10 07:32:00',
          ftime: '2018-05-10 07:32:00',
          context: ' []',
          location: ''
        },
        {
          time: '2018-05-10 02:03:00',
          ftime: '2018-05-10 02:03:00',
          context: '[], []',
          location: ''
        },
        {
          time: '2018-05-09 23:05:00',
          ftime: '2018-05-09 23:05:00',
          context: ' []',
          location: ''
        },
        {
          time: '2018-05-09 21:21:00',
          ftime: '2018-05-09 21:21:00',
          context: '[], []',
          location: ''
        },
        {
          time: '2018-05-09 13:07:00',
          ftime: '2018-05-09 13:07:00',
          context: ' ',
          location: ''
        },
        {
          time: '2018-05-09 12:25:03',
          ftime: '2018-05-09 12:25:03',
          context: '',
          location: ''
        },
        {
          time: '2018-05-09 12:22:24',
          ftime: '2018-05-09 12:22:24',
          context: 'HLA',
          location: ''
        },
        {
          time: '2018-05-08 21:36:04',
          ftime: '2018-05-08 21:36:04',
          context: '',
          location: ''
        }
      ]
      // console.log(this.progressInfo)
      this.progressVisible = true
    }

  }
}
</script>

<style lang="less" scoped>

.el-cascader {
  width: 100%;
}
</style>
